body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}




.logo {
  color: #ebe5d8;
  background-color: #815e5f;
  padding-top: 20px;
  padding-bottom: 20px;
  width: 33%;
  font-family: 'Sigmar One', cursive;
  float: left;
  min-height: 75px;
}

.logo h1 {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 30px;
}
.console {
  color: #ebe5d8;
  background-color: #815e5f;
  padding-top: 25px;
  padding-bottom: 25px;
  width: 34%;
  font-family: 'Sigmar One', cursive;
  float:left;
  min-height: 75px;
}

.console h2 {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 20px;
}
.scoreboard {
  color: #ebe5d8;
  background-color: #815e5f;
  padding-top: 20px;
  padding-bottom: 20px;
  width: 33%;
  font-family: 'Sigmar One', cursive;
  float:left;
  min-height: 75px;
}

.scoreboard h2 {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 30px;
}
.jumbotron {
  background-image: url(/Clicky-Game-Two/static/media/warcraft.58dc21d2.jpg);
  background-size: 480px 167px;
  font-family: 'Sigmar One', cursive;
  color: white;
  padding-top: 20px;
  padding-bottom: 10px;
  clear:both;
}

.jumbotron p {
  font-size: 30px;
  text-shadow: 2px 2px 4px #000000;
}

.gameScreen {
  color: #ebe5d8;
  font-family: 'Sigmar One', cursive;
  width: 1200px;
  text-align: center;
  margin: 0 auto;
}

.list-group-item {
  font-size: 30px;
  float: left;
  padding-top: 10px;
  padding-bottom: 10px;
  margin-bottom: 20px;
  margin-left: 30px;
  margin-right: 0;
  border: 0;
}

.list-group-item img {
  width: 80%;
}


.footer {
  color: #815e5f;
  background-color: #ebe5d8;
  padding-top: 20px;
  padding-bottom: 20px;
  font-family: 'Sigmar One', cursive;
  clear: both;
  min-height: 75px;
}

.footer h1 {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 30px;
}
